class Message {
  constructor(left, right, top, bottom, pos, text, text2) {
    this.left = left;
    this.right = right;
    this.top = top;
    this.bottom = bottom;
    this.pos = pos;
    this.text = text;
    this.text2 = text2;
  }
}

module.exports = Message;



